@import url(https://fonts.googleapis.com/css2?family=Mulish:wght@400;700;800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* Home page */
.home-page section:nth-child(even) {
  background-color: #F5F5F5;
}

/* Section 1 (Summary) and Section 2 (Why it's important) */
.summary, .why, .try-it {
  height: 370px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

.summary *, .why *, .try-it * {
  width: 50%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}

/* consider adding this as a universal rule for h1 and p */
.summary h1, .summary p, .why h1, .why p {
  text-align: left;
}

/* Section 3 - How it works*/
/* .how-it-works img {
  width: 100%;
} */

/* all sections */
h1 {
  font-weight: 700;
}

img {
  width: 100%;
}
.about-page {
  /* width: 50%;
  display: flex;
  justify-content: center; */
}

.overview {
  height: 500px;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;

}

.overview * {
  width: 60%;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
}
/* Navbar */
.logo {
  margin: 3px;
}

nav {
  background-color: white;
  border-bottom: solid #4A69D9 3px;
  /* margin: 5px; */
  padding: 30px;
  padding-top: 15px;
  padding-bottom: 20px;
  
  display: -webkit-flex;
  
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-justify-content: space-between;
          justify-content: space-between;
  -webkit-align-items: center;
          align-items: center;
}

nav a:hover, nav span {
  color: black;
  text-decoration: none; 
  cursor: pointer;
}

a, #nav-links {
  color: black;
  font-size: 18px;
  padding-left: 5px;
  padding-right: 5px;
  text-decoration: none; 
}

.navbar-brand {
  font-weight: bold;
  font-size: 44px;
  text-decoration: none;
}

#nav-links {
  font-size: 20px;
  padding-top: 10px;
}


